<template>
  <div id="app">
    <Header></Header>
    <div class="container">
      <Sidebar></Sidebar>
      <div class="main">
        <router-view/>
      </div>
    </div>
  </div>
</template>

<script>
import Header from './components/Header'
import Sidebar from './components/Sidebar'

export default {
  name: 'App',
  components: {
    Header,
    Sidebar
  }
}
</script>

<style lang="sass">
@import './style/index.sass'
#app
  width: 100%
  .main
    width: calc(100% - 300px)
    margin-left: 40px
@media screen and (max-width: $media-size-phone)
  #app
    .main
      width: 100%
      margin: 0px
      padding: 0px 40px
</style>



