<template>
  <div class="home">
    <div class="top">
      <div class="result">Showing <span>{{ filtedArticles.length }}</span> results by…</div>
      <div class="hash-tag-groups">
        <div v-for="(hashtag,index) in filterKeywords" class="hash-tag animated bounceIn">
          {{ hashtag.Name }}
          <i class="far fa-times-circle" @click="deleteFilterKeywords(index)"></i>
        </div>
        <div v-if="keyword !== ''" class="hash-tag animated bounceIn">
          {{ keyword }}
          <i class="far fa-times-circle" @click="clearAndReFetch"></i>
        </div>
      </div>
    </div>
    <div class="card-lists">
      <card v-for="(article,index) in filtedArticles.slice(pageStart, pageStart + countOfPage)" :article="article" :key="index"></card>
    </div>
    <nav>
      <ul class="pagination" >
        <li v-bind:class="{'disabled': (currPage === 1)}" @click.prevent="setPage(currPage - 1)">
          <a class="page-link" href="#" aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li v-for="n in totalPage"
            v-bind:class="{'active': (currPage === (n))}" 
            @click.prevent="setPage(n)"><a class="page-link" href="#">{{n}}</a></li>
        <li v-bind:class="{'disabled': (currPage === totalPage)}" @click.prevent="setPage(currPage + 1)">
          <a class="page-link" href="#" aria-label="Next">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
import Card from './Card'
import { mapState, mapMutations, mapActions, mapGetters } from 'vuex'
export default {
  name: 'Home',
  data () {
    return {
      countOfPage: 10,
      currPage: 1,
    }
  },
  mounted() {
    // this.$store.dispatch('fetchAtricles');
    this.fetchAtricles()
  },
  components: {
    Card
  },
  computed: {
    ...mapState(["articles", "filterKeywords","keyword"]),
    ...mapGetters(['filtedArticles']),
    pageStart: function(){
        return (this.currPage - 1) * this.countOfPage;
      },
    totalPage: function(){
      this.currPage = 1;
      return Math.ceil(this.filtedArticles.length / this.countOfPage);
    }
  },
  methods: {
    ...mapMutations(["deleteFilterKeywords","handlekeyword"]),
    ...mapActions(['fetchAtricles','clearAndReFetch']),
    setPage: function(idx){
      if( idx <= 0 || idx > this.totalPage ){
        return;
      }
      this.currPage = idx;
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="sass">
@import '../style/components/home.sass'
</style>



