<template>
	<div class="header">
    <div class="container">
      <div class="logo">
        <img src="../assets/logo.png" alt="">
      </div>
      <div class="search-box">
        <i class="fas fa-search"></i>
        <input type="text" placeholder="Explore your own activities" v-model="searchkeyword" @keyup.enter="addKeywordInState">
      </div>
    </div>
    <div class="media-phone">
      <div class="search-box">
        <i class="fas fa-search"></i>
        <input type="text" placeholder="Explore your own activities" v-model="searchkeyword" @keyup.enter="addKeywordInState">
      </div>
    </div>
	</div>
</template>

<script>
import { mapMutations,mapActions } from 'vuex'
export default {
  name: 'Header',
  data () {
    return {
      searchkeyword: ''
    }
  },
  methods: {
    ...mapActions(['keyWordAndFetch']),
    addKeywordInState() {
      if(this.searchkeyword.trim() !== '')
        this.keyWordAndFetch(this.searchkeyword);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="sass">
@import '../style/components/header.sass'
</style>



