<template>
  <a class="card animated fadeIn" :href="article.Website" target="_blank">
    <div class="left-image">
      <div class="image" :style="{backgroundImage: `url(${article.Picture1})`}"></div>
    </div>
    <div class="rigth-information">
        <div class="title">{{ article.Name }}</div>
        <div class="description">{{ article.Description }}</div>
        <div class="type">
          <div class="author">{{ article.Picdescribe1 }}</div>
          <div class="hash-tag">Entertainment</div>
        </div>
        <div class="date-and-locat">
          <div class="location">
            <i class="fas fa-map-marker"></i>
            {{ article.Add }}
          </div>
          <div class="date">
            <i class="far fa-calendar-alt"></i>
            {{ article.Opentime }}
          </div>
        </div>
    </div>
  </a>
</template>

<script>
export default {
  name: 'Card',
  props: ['article'],
  data () {
    return {
      
    }
  },
  methods: {
    openWebSite(url) {
      location.href = url;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="sass">
@import '../style/components/card.sass'
</style>



