<template>
	<div class="side-bar">
    <input type="checkbox" id="switch-location"/>
    <div class="sidebox location">
      <div class="title">Location</div>
      <label class="switch-btn" for="switch-location"></label>
      <div class="content">
        <select name="location" id="location" v-model="selectedLocation" @change="addLocalKeywordInState">
          <option v-for="(location,index) in locations" :value="location">{{ location }}</option>
        </select>
      </div>
    </div>
    <input type="checkbox" id="switch-Date"/>
    <div class="sidebox date">
      <div class="title">Date</div>
      <label class="switch-btn" for="switch-Date"></label>
      <div class="content">
        <div class="date-interval">
          <div class="from">
            <div class="text">from</div>
            <input type="date" :value="startDate" @change="HandleStartDay"/>
          </div>
          <div class="to">
            <div class="text">to</div>
            <input type="date" :value="endDate"  @change="HandleEndDay"/>
          </div>
        </div>
      </div>
    </div>
    <input type="checkbox" id="switch-Categories"/>
    <div class="sidebox categories">
      <div class="title">Categories</div>
      <label class="switch-btn" for="switch-Categories"></label>
      <div class="content">
        <ul class="cate-lists">
          <li class="cate">
            <input type="checkbox" name="all" id="all" value='免費參觀' v-model="isFreeVisit" @click="toggleFreeVisit">
            <label for="all">免費參觀</label>
          </li>
          <li class="cate">
            <input type="checkbox" name="Entertainment" id="Entertainment" value="全天候開放" v-model="isOpenAllDay" @click="toggleOpenAllDay">
            <label for="Entertainment">全天候開放</label>
          </li>
          <li class="cate">
            <input type="checkbox" name="Food" id="Food" value="景點需付費" v-model="isNeedPay" @click="toggleNeedPay">
            <label for="Food">景點需付費</label>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex';
export default {
  name: 'Sidebar',
  data () {
    return {
      selectedLocation: "所有地區",
      isFreeVisit: false,
      isOpenAllDay: false,
      isNeedPay: false,
      startDate: '',
      endDate: '',
      locations: [
      '所有地區','三民區','美濃區','大樹區',
      '小港區','六龜區','仁武區',
      '內門區','左營區','田寮區',
      '永安區','甲仙區','杉林區',
      '那瑪夏','岡山區','鳳山區',
      '美濃區','前鎮區','新興區',
      '苓雅區','茂林區','茄萣區',
      '旗津區','桃源區','楠梓區',
      '前金區','鼓山區']
    }
  },
  methods: {
    ...mapMutations(['addFilterKeywords',
    'toggleFreeVisiteInfilterKeywords',
    'toggleLocatFilterKeywords',
    'toggleOpenAllDayInfilterKeywords',
    'toggleNeedPayDayInfilterKeywords',
    'setStartDate',
    'setEndDate']),
    addLocalKeywordInState() {
      this.toggleLocatFilterKeywords(this.selectedLocation);
    },
    toggleFreeVisit() {
      this.isFreeVisit = !this.isFreeVisit;
      this.toggleFreeVisiteInfilterKeywords(this.isFreeVisit)
    },
    toggleOpenAllDay() {
      this.isOpenAllDay = !this.isOpenAllDay;
      this.toggleOpenAllDayInfilterKeywords(this.isOpenAllDay)
    },
    toggleNeedPay() {
      this.isNeedPay = !this.isNeedPay;
      this.toggleNeedPayDayInfilterKeywords(this.isNeedPay)
    },
    HandleStartDay(e) {
      console.log(e.target.value)
      if(this.endDate === ''){
        this.startDate = e.target.value;
        this.setStartDate(e.target.value);
        return
      }
      if((Date.parse(e.target.value)).valueOf() > (Date.parse(this.endDate)).valueOf()) {
        alert('開始日期不得大於結束日期');
        this.startDate = '';
        return
      }
      this.startDate = e.target.value;
      this.setStartDate(e.target.value);
    },
    HandleEndDay(e) {
      console.log(e.target.value)
       if(this.startDate === ''){
        this.endDate = e.target.value;
        this.setEndDate(e.target.value);
        return
      }
      if((Date.parse(this.startDate)).valueOf() > (Date.parse(e.target.value)).valueOf()) {
        alert('結束日期不得小於開始日期');
        this.endDate = '';
        return
      }
      this.endDate = e.target.value;
      this.setEndDate(e.target.value);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="sass">
@import '../style/components/sidebar.sass'
</style>


