import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const api = 'https://data.kcg.gov.tw/api/action/datastore_search?resource_id=92290ee5-6e61-456f-80c0-249eae2fcc97';

const state = {
    articles: [],
    filterKeywords: [],
    keyword: '',
    loading: false,
    startDate: '',
    endDate: ''
}

// state裡面的computed 在這邊做搜尋的動作
const getters = {
    filtedArticles(state) {
      let { articles, filterKeywords } = state;
      let filterArray = JSON.parse(JSON.stringify(articles));
      if(filterKeywords.length > 0) {
        filterArray = filterArray.filter(item => filterKeywords.every(keyword => {
          if(keyword.type === 'Ticketinfo' && keyword.Name === '景點需付費' && item[keyword.type].length > 0
          && item[keyword.type] !== '免費參觀'){
            return true
          }
          return item[keyword.type] === keyword.Name
        }))
      }
      if(state.startDate !== "" && state.endDate !== "") {
        let start = (Date.parse(state.startDate)).valueOf();
        let End = (Date.parse(state.endDate)).valueOf();
        filterArray = filterArray.filter(item => {
          let target = (Date.parse(item.Changetime)).valueOf();
          console.log(start);
          console.log(target);
          console.log(End);
          console.log(target > start && target < End);
          return target > start && target < End;
        })
      }
      return filterArray;
    }
}

// 不能在actions中指定state 要發出mutations
// 呼叫actions 用 dispatch
// actions的方法 第一個參數 都是 context 包含了mutations的commit 及 actions的 dispatch 和其他store的方法  用在module
const actions = {
    //把 context 裡的 commit 解構出來用
   fetchAtricles({ commit }) {
    commit('setLoading', true);

    fetch(api,{method: 'GET'})
    .then(response => response.json())
    .then( response => {
      commit("getAllArticles", response.result.records);
      commit('setLoading', false);
    })
    .catch(err => {console.log(err)})
   },

   fetchAtriclesByKeyWords({ commit }, keyword) {
    commit('setLoading', true);

    fetch(api + `&q=${keyword}`,{method: 'GET'})
    .then(response => response.json())
    .then( response => {
      commit("getAllArticles", response.result.records);
      commit('setLoading', false);
    })
    .catch(err => {console.log(err)})
   },

    clearAndReFetch({commit, dispatch}) {
      commit("handlekeyword");
      dispatch("fetchAtricles");
    },

   keyWordAndFetch({commit, dispatch}, keyword) {
      if(keyword.trim()===""){
        dispatch("clearAndReFetch", keyword);
      }
      commit("addFilterKeywords",keyword);
      dispatch("fetchAtriclesByKeyWords", keyword);
    }
}

// 呼叫mutations 用 commit
const mutations = {
    setLoading(state, loading){
      state.loading = loading;
    },
    //搜尋關鍵字處裡
    addFilterKeywords(state, keyword){
      state.keyword = keyword;
    },
    deleteFilterKeywords(state, index){
      state.filterKeywords.splice(index, 1);
    },
    handlekeyword(state){
      state.keyword = '';
    },
    //文章載入
    getAllArticles(state, atricles) {
      state.articles = atricles;
    },
    toggleFreeVisiteInfilterKeywords(state, bool) {
      if(bool){
        state.filterKeywords.push({type: 'Ticketinfo', Name: '免費參觀'});
      } else {
        state.filterKeywords = state.filterKeywords.filter(item => item.type!=='Ticketinfo'&&item.Name!=='免費參觀');
      }
    },
    toggleLocatFilterKeywords(state, locate){
      if(locate==="所有地區") {
        state.filterKeywords = state.filterKeywords.filter(item=>item.type !== 'Zone')
      } else if (state.filterKeywords.some(item=>item.type === 'Zone')) {
        state.filterKeywords = state.filterKeywords.map(item => {
          if(item.type === 'Zone') item.Name = locate;
          return item;
        })
      } else {
        state.filterKeywords.push({type: 'Zone', Name: locate});
      }
    },
    toggleOpenAllDayInfilterKeywords(state, bool){
      if(bool){
        state.filterKeywords.push({type: 'Opentime', Name: '全天候開放'});
      } else {
        state.filterKeywords = state.filterKeywords.filter(item => item.type !== 'Opentime');
      }
    },
    toggleNeedPayDayInfilterKeywords(state, bool) {
      if(bool){
        state.filterKeywords.push({type: 'Ticketinfo', Name: '景點需付費'});
      } else {
        state.filterKeywords = state.filterKeywords.filter(item => item.type!=='Ticketinfo'&&item.Name!=='景點需付費');
      }
    },
    setStartDate(state, date) {
      state.startDate = date
    },
    setEndDate(state, date) {
      state.endDate = date
    }
}


export default new Vuex.Store({
    state,
    getters,
    actions,
    mutations
})



